
import "./AboutMe.scss";
import { Container, Row, Col } from 'react-bootstrap';
import picture1 from "../images/selfPhoto.jpg";
import picture2 from "../images/selfPhoto2.jpg";
import 'bootstrap/dist/css/bootstrap.min.css';

const AboutMe = () => {
    return (

        <Container className="mt-5">
            <Row>
                <Col xs={12} md={6}>
                    <h1 className="mb-5">Hana Jedlikov</h1>
                    <h4>
                        Kdo jsem?
                    </h4>
                    <p>

                        Jmenuji se Hana Jedlikov a v oboru fotografie se pohybuji u od dtstv. Ve zaalo, u kdy mi bylo 11 let a j dostala svj prvn fotoapart. Od t doby se fotografie stala moji ivotn cestou.
                    </p>
                    <p>
                        Nen to pro m pouze konek i prce. Je to dalo by se ci mj ivotn smr, ve kterm jsem se rozhodla zdokonalovat a vloit do toho kus sebe. Sv znalosti v oboru jsem rozvjela jak na stedn, tak i na vysok kole.
                    </p>
                </Col>

                <Col xs={12} md={6} align="center" className="al">
                    <img className="about-me-photo" src={picture2} alt="Hanka" />
                </Col>
            </Row>

            <Row className="align-items-center flex-column-reverse flex-md-row">
                <Col xs={12} md={6} align="center">
                    <img className="about-me-photo" src={picture1} alt="Hanka" />
                </Col>
                <Col xs={12} md={6}>
                    <h4>
                        Pstup ke klientm je pro m to nejdleitj!
                    </h4>
                    <p>
                        Dky tmto zkuenostem jsem doshla svho fotografickho stylu, a pedevm si ujasnila, jak pistupovat ke klientm. Je pro m opravdu dleit vkldat do sv prce srdce a vnmat vs individuln. Zamuji se tedy jak na lidi se zkuenostmi s focenm, tak samozejm i na ty, kte chtj vystoupit z komfortn zny a splnit si poprv svj sen stt ped objektivem.
                    </p>
                    <p>
                        Podstatn je zde zmnit i to, e si nevybrm, koho budu fotit! Nezle tedy na vaem vku ani napklad na typu postavy. Moj prac je ukzat kadmu z vs, e jste fotogenit a splnit vae oekvn o sluivch a pirozench fotkch. Pesn to je ta kouzeln pleitost, vyzdvihnout to, v em jste zrovna vy jedinen.
                    </p>
                    <p>
                        N spolen strven as bude pizpsoben kadmu zvl. S nervozitou se zde samo sebou pot, ale my ve pokome a spolen as si uijeme s smvem na plno. Zakldm si na ptelsk atmosfe.
                    </p>
                    <p>
                        Bav m plnit vm vae pedstavy, tvoit seznn nebo tematick scny, a pedevm m bav poznvat vs a trvit s vmi as. Neexistuje u m dn spch ani ntlak a nejvt odmnou je pro m vae spokojenost. pln nejkrsnj pocit je pro m ten, kdy vidm, jak se vae nervozita mn v sebevdom a odchzte usmvav a pln pozitivn energie.
                    </p>
                </Col>
            </Row>
            <Row>
                <Col xs={12} md={6}>
                    <h4>
                        Co vlastn fotm?
                    </h4>
                    <p>

                        Nejdleitj je pro m vae pedstava. Kde a co budeme fotit je zcela na vs a j vm pn rda splnm. Pokud vak pedstavu nemte, dejte mi volnou ruku a o dokonal vsledek se postarm.

                        Nedoku ci, co fotm nejradji. Kad focen je pro m obrovskm zitkem, nehled na to, zda fotm jednotlivce, rodiny, dti, mazlky, firemn akce, svatby...
                    </p>
                    <p>
                        Z mho portfolia lze z cela jist poznat, e nejvt oblibu mm ve focen v exteriru. A u v mstskm prosted, kde si meme vyhrt s modern i historickou architekturou, tak hlavn v prod. Napklad ena a proda je to nejkrsnj a jedinen propojen, kter nsledn vyzauje nesmrnou pirozenost a jemnost.
                    </p>
                    <p>
                        Nabzm i focen uvnit, v malm ateliru, kter je zaloen pedevm na konceptu jednoduchosti a minimalismu. Je tedy vhodn pro kadho, kdo jednoduchost miluje. Fot se zde jak jednotlivci a pry, tak i rodiny i domc mazlci.
                    </p>
                    <p>
                        Musm vak zdraznit, e a u si vyberete focen v interiru i v exteriru, ve m sv kouzlo a vsledek bude stt za to!
                    </p>
                    <p>
                        Pokad je mi ct, bt vm po boku pi dleitch dnech vaeho ivota a nejrznjch akcch, pi kterch pro vs mohu zachytit a uchovat vzpomnky na cel ivot. (A u to jsou svatby, narozeniny, ktiny, thotenstv nebo narozen potomka ...)
                    </p>
                </Col>

                <Col xs={12} md={6} align="center" className="al">
                    <img className="about-me-photo" src={picture2} alt="Hanka" />
                </Col>
            </Row>
            <Row className="align-items-center flex-column-reverse flex-md-row">
                <Col xs={12} md={6} align="center">
                    <img className="about-me-photo" src={picture1} alt="Hanka" />
                </Col>
                <Col xs={12} md={6}>
                    <h4>
                        Sebevdom se d budovat i formou fotografie.
                    </h4>
                    <p>
                        Pipadte si nefotogenit, ale toute po svch krsnch snmcch? Brn vm strach? U m jste na sprvnm mst!
                    </p>
                    <p>
                        asto se setkvm s tm, e pedevm eny odmtaj focen i pesto, e je to jejich sen, a to jen z toho dvodu, e si nepijdou fotogenick anebo maj strach z pzovn. Od toho tady jsem a celm

                        focenm vs budu s radost provzet. Poradm s mylenkou, s tm, co na sebe, a dokonce i pijdeme na vhodn pzy a vrazy pmo pro vs. Je dleit vnmat jedinenost a originalitu kadho lovka.
                    </p>
                    <p>
                        konzultovat, abychom fotografie vyladili k dokonalosti. Sta vykroit ze sv komfortn zny a dt sob anci.
                    </p>
                    <p>
                        k vdy km, dokud to lovk nezkus, nikdy nezjist, co v sob ukrv.
                    </p>
                </Col>
            </Row>
            <Row>
                <Col className="text-align-center">
                    <h4>
                        Pr slov na zvr.
                    </h4>
                    <p>
                        Focen nen jen o strachu a nervozit, je to pedevm o zitku, zkuenosti, pozitivn energii, a pedevm o zachycen vzpomnek. Spolen as si uijeme naplno a vsledn fotografie budou i pamtkou na v den ped objektivem.

                        Pokud vs zaujala moje tvorba a pstup, sta se ozvat a budu vm pln k dispozici.
                    </p>
                    <p>
                        Tm se vs a na to, co krsnho spolen vytvome.
                    </p>
                    <p>S lskou,</p>
                    <p>Hanka</p>
                </Col>
            </Row>
        </Container>
    )
}

export default AboutMe;